import { Component, OnDestroy, OnInit, TemplateRef } from '@angular/core';
import { ApiService } from '../../api.service';
import { Subject } from 'rxjs';
import { BsModalService, BsModalRef } from 'ngx-bootstrap/modal';
import {Router} from '@angular/router';
@Component({
  selector: 'app-dmp-list',
  templateUrl: 'dmp-list.component.html',
  styleUrls: ['dmp-list.component.css']
})
export class DmpListComponent implements OnInit {
	data: any = [];
	dtOptions: DataTables.Settings = {};
  // We use this trigger because fetching the list of persons can be quite long,
  // thus we ensure the data is fetched before rendering
  dtTrigger = new Subject();
   modalRef: BsModalRef;
   message: string = '';
   deleteId: any = 0;
  constructor(
  	public api: ApiService,
    public modalService: BsModalService,
    public router: Router
  	) { }

  ngOnInit(): void {
  	this.dtOptions = {
      pagingType: 'full_numbers',
      pageLength: 5
    };
  	this.api.provideData({
  		type: 'getlistdisway'
  	}, 'data-adm-api').subscribe((resp)=>{
  		this.data = resp;
  		console.log(this.data);
  		this.dtTrigger.next();
  	});
  }

  getData(){
    this.data = [];
    this.api.provideData({
      type: 'getlistdisway'
    }, 'data-adm-api').subscribe((resp)=>{
      this.data = resp;
      console.log(this.data);
    });
  }

  ngOnDestroy(): void {
    // Do not forget to unsubscribe the event
    this.dtTrigger.unsubscribe();
  }

  openModal(template: TemplateRef<any>, id) {
    this.modalRef = this.modalService.show(template, {class: 'modal-sm'});
    this.deleteId = id;
  }

  deleteDisway(){
    this.api.provideData({
      type: 'deletedisway',
      disway_id: this.deleteId
    }, 'data-adm-api').subscribe((resp)=>{
      this.getData();
    });
  }

  confirm(): void {
    this.deleteDisway();
    this.message = 'Confirmed!';
    this.modalRef.hide();
  }
 
  decline(): void {
    this.message = 'Declined!';
    this.modalRef.hide();
  }

  editDisway(item){
    this.router.navigate(['/dmp-edit'], {state: {data: item }});
  }

}
